.LifeContentPage {
  text-align: start;
  overflow-x: hidden; }
  .LifeContentPage-ContentWrapper {
    margin-top: 15px;
    display: -ms-flexbox;
    display: flex;
    -webkit-transition: -webkit-transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: -webkit-transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    -o-transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955), -webkit-transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955); }
  .LifeContentPage-leftArrow {
    position: absolute;
    top: 42%;
    left: 10px;
    z-index: 1000; }
  .LifeContentPage-rightArrow {
    position: absolute;
    top: 42%;
    right: 10px;
    z-index: 1000; }
  .LifeContentPage-postWrapper {
    margin: 0 15px;
    padding-bottom: 3rem; }
  .LifeContentPage-title {
    margin: 0;
    height: 40px;
    line-height: 40px;
    color: #262626;
    font-size: 2.4vh; }
  .LifeContentPage-content {
    margin: 0;
    padding-bottom: 5px;
    font-size: 2vh; }
  .LifeContentPage-tag {
    color: #003569;
    font-size: 1.95vh; }



